"use client"

import { useState, useEffect } from "react"
import { Save } from "lucide-react"

interface Settings {
  theme: "light" | "dark"
  notifications: boolean
  emailReminders: boolean
  soundEnabled: boolean
  defaultBlockDuration: string
  autoBreaks: boolean
  breakDuration: string
}

export default function SettingsPage() {
  const [settings, setSettings] = useState<Settings>({
    theme: "light",
    notifications: true,
    emailReminders: true,
    soundEnabled: true,
    defaultBlockDuration: "90",
    autoBreaks: true,
    breakDuration: "15",
  })
  const [isSaved, setIsSaved] = useState(false)

  useEffect(() => {
    const stored = localStorage.getItem("appSettings")
    if (stored) {
      setSettings(JSON.parse(stored))
    }
  }, [])

  const handleSaveSettings = () => {
    localStorage.setItem("appSettings", JSON.stringify(settings))
    setIsSaved(true)
    setTimeout(() => setIsSaved(false), 3000)
  }

  const handleChange = (key: keyof Settings, value: any) => {
    setSettings((prev) => ({ ...prev, [key]: value }))
  }

  return (
    <div className="p-4 lg:p-8">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-foreground mb-2">Settings</h1>
        <p className="text-muted-foreground">Customize your experience</p>
      </div>

      <div className="max-w-2xl space-y-6">
        {/* Notifications */}
        <div className="bg-card rounded-xl shadow-md p-6 border border-border">
          <h3 className="text-lg font-bold text-foreground mb-4">Notifications</h3>
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="font-medium text-foreground">Push Notifications</p>
                <p className="text-sm text-muted-foreground">Receive push notifications for reminders</p>
              </div>
              <input
                type="checkbox"
                checked={settings.notifications}
                onChange={(e) => handleChange("notifications", e.target.checked)}
                className="w-5 h-5 cursor-pointer"
              />
            </div>
            <div className="flex items-center justify-between">
              <div>
                <p className="font-medium text-foreground">Email Reminders</p>
                <p className="text-sm text-muted-foreground">Get email reminders for upcoming blocks</p>
              </div>
              <input
                type="checkbox"
                checked={settings.emailReminders}
                onChange={(e) => handleChange("emailReminders", e.target.checked)}
                className="w-5 h-5 cursor-pointer"
              />
            </div>
            <div className="flex items-center justify-between">
              <div>
                <p className="font-medium text-foreground">Sound Effects</p>
                <p className="text-sm text-muted-foreground">Play sound for notifications</p>
              </div>
              <input
                type="checkbox"
                checked={settings.soundEnabled}
                onChange={(e) => handleChange("soundEnabled", e.target.checked)}
                className="w-5 h-5 cursor-pointer"
              />
            </div>
          </div>
        </div>

        {/* Time Blocking Preferences */}
        <div className="bg-card rounded-xl shadow-md p-6 border border-border">
          <h3 className="text-lg font-bold text-foreground mb-4">Time Blocking</h3>
          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-foreground mb-2">Default Block Duration (minutes)</label>
              <input
                type="number"
                value={settings.defaultBlockDuration}
                onChange={(e) => handleChange("defaultBlockDuration", e.target.value)}
                min="30"
                max="240"
                step="15"
                className="w-full px-4 py-2 border border-border rounded-lg bg-input text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
              />
            </div>
            <div className="flex items-center justify-between">
              <div>
                <p className="font-medium text-foreground">Auto Breaks</p>
                <p className="text-sm text-muted-foreground">Automatically schedule breaks between blocks</p>
              </div>
              <input
                type="checkbox"
                checked={settings.autoBreaks}
                onChange={(e) => handleChange("autoBreaks", e.target.checked)}
                className="w-5 h-5 cursor-pointer"
              />
            </div>
            {settings.autoBreaks && (
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">Break Duration (minutes)</label>
                <input
                  type="number"
                  value={settings.breakDuration}
                  onChange={(e) => handleChange("breakDuration", e.target.value)}
                  min="5"
                  max="60"
                  step="5"
                  className="w-full px-4 py-2 border border-border rounded-lg bg-input text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
                />
              </div>
            )}
          </div>
        </div>

        {/* Theme Settings */}
        <div className="bg-card rounded-xl shadow-md p-6 border border-border">
          <h3 className="text-lg font-bold text-foreground mb-4">Appearance</h3>
          <div>
            <label className="block text-sm font-medium text-foreground mb-3">Theme</label>
            <div className="flex gap-4">
              {["light", "dark"].map((theme) => (
                <label key={theme} className="flex items-center gap-2 cursor-pointer">
                  <input
                    type="radio"
                    name="theme"
                    value={theme}
                    checked={settings.theme === theme}
                    onChange={(e) => handleChange("theme", e.target.value as any)}
                    className="w-4 h-4"
                  />
                  <span className="text-foreground capitalize font-medium">{theme}</span>
                </label>
              ))}
            </div>
          </div>
        </div>

        {/* Save Button */}
        <div className="flex gap-3">
          <button
            onClick={handleSaveSettings}
            className="flex-1 flex items-center justify-center gap-2 bg-primary hover:bg-primary/90 text-primary-foreground px-6 py-3 rounded-lg font-medium transition-colors"
          >
            <Save size={20} />
            Save Settings
          </button>
          {isSaved && (
            <div className="flex items-center justify-center px-4 py-3 bg-green-100 text-green-700 rounded-lg font-medium">
              Settings saved!
            </div>
          )}
        </div>
      </div>
    </div>
  )
}
